nav {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin-top: 10px;
    margin-bottom: 10px;
}

.item {
    border: 2px solid teal;
    padding: 5px;
    border-radius: 10px;
}

.item a {
    color: teal;
    text-decoration: none;

}

.item a.activeLink {
    color: dodgerblue;;
}

.item a:hover {
    opacity: 0.6;
    color: dodgerblue;;
    transition: .3s all ease;
}

.item:hover {
    background: #fff;
    color: #222222;
    transition: .3s all ease;
}