.buttonBlock {
    background: none;
    margin-right: 10px;
    border: 2px solid teal;
    border-radius: 10px;
}

.scoreBlock {
    text-align: center;
    width: 80px;
    margin-right: 10px;
    border: 2px solid teal;
    background: none;
    border-radius: 10px;
}

.scoreboardButton {
    background: none;
    border: 2px solid teal;
    text-align: center;
    border-radius: 10px;
    margin-right: 10px;
}

.popUp {
    background: none;
    align-items: center;
    justify-content: center;
    height: 150px;
    width: 150px;
    margin: auto;
    margin-top: 10px;
    border: 2px solid teal;
    border-radius: 10px;
}
