.cardsElements {
    height: 745px;
    width: 750px;
    border: 2px solid teal;
    margin: auto;
    margin-top: 20px;
    border-radius: 10px;
}

.example-enter {
    opacity: 0.01;
}

.example-enter.example-enter-active {
    opacity: 1;
    transition: opacity 300ms ease-in;
}

.example-leave {
    opacity: 1;
}

.example-leave.example-leave-active {
    opacity: 0.01;
    transition: opacity 300ms ease-in;
}